@use '../utils/mixins' as *;

.title {
  font-family: Poppins, sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 36px;
  letter-spacing: -1.5px;
  text-align: center;

  color: rgba(255, 255, 255, 1);

  @include for-ipad {
    font-size: 64px;
    line-height: 64px;
  }

  @include for-desktop {
    font-size: 80px;
    line-height: 80px;
  }

  &--secondary {
    font-family: Poppins, sans-serif;
    font-size: 32px;
    font-weight: 500;
    line-height: 32px;
    letter-spacing: -1.5px;
    text-align: left;
    margin-bottom: 32px;

    @include for-ipad {
      font-size: 48px;
      line-height: 48px;
      text-align: center;
      margin-bottom: 48px;
    }

    @include for-desktop {
      font-size: 64px;
      line-height: 64px;
      letter-spacing: -1.5px;
      text-align: center;
      margin-bottom: 56px;
    }
  }

  &--third {
    font-family: Poppins, sans-serif;
    font-size: 24px;
    font-weight: 700;
    line-height: 33.6px;
    text-align: left;
    margin-bottom: 8px;

    grid-column: 1 / 3;

    @include for-ipad {
      grid-column: 1 / 6;
    }

    @include for-desktop {
      grid-column: 1 / 7;
    }
  }

  &--fourth {
    font-family: Poppins, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;
    text-align: left;
    margin-bottom: 8px;
  }

  // &--position {
  //   text-align: center;
  //   margin-bottom: 48px;
  // }
}
